#clock-container {
    background-color: rgb(186, 212, 249);
    font-size: 2rem;
    width: 40%;
    margin: 0 auto;
    margin-top: 20px;
    border-radius: 6px;
}

.test-class {
    white-space: pre;
    font-family: monospace;
}

.todo-list {
    background-color: white;
    margin: auto;
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 3px solid #efefef;
    box-shadow:
    /* The top layer shadow */
        0 1px 1px rgba(0,0,0,0.15),
        /* The second layer */
        0 10px 0 -5px #eee,
        /* The second layer shadow*/
        0 10px 1px -4px rgba(0,0,0,0.15),
        /* The third layer */
        0 20px 0 -10px #eee,
        /* The third layer shadow*/
        0 20px 1px -9px rgb(0,0,0,0);
    padding: 30px;
}

.todo-item {
    background-color: white;
    border-bottom:2px solid rgb(191, 191, 191);
}
